import { Settings, Edit2, ChevronRight, MapPin, Star, Zap, User } from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress"; // Va trebui să vă asigurați că aveți componenta Progress

// O componentă mică pentru afișarea secțiunilor (Skills, Looking to learn)
const ProfileSection = ({ title, children }) => (
  <div className="space-y-3">
    <h3 className="text-lg font-semibold text-primary">{title}</h3>
    <div className="flex flex-wrap gap-2">
      {children}
    </div>
  </div>
);

// O componentă mică pentru butoanele de navigare din josul paginii
const NavButton = ({ icon: Icon, text, onClick }) => (
  <Button
    variant="ghost"
    className="w-full justify-between h-14 rounded-xl px-4 text-primary hover:bg-muted/50 transition-colors"
    onClick={onClick}
  >
    <div className="flex items-center space-x-3">
      <Icon className="h-5 w-5" />
      <span className="text-base">{text}</span>
    </div>
    <ChevronRight className="h-5 w-5" />
  </Button>
);

export default function ProfilePage() {
  const user = {
    name: "User Test",
    role: "Web developer",
    location: "Iași, România",
    rating: 4.8,
    description: "Search in Figma and have an employment, clarify, make the right decisions, and always be open-minded related to designs, research, or usability testing.",
    skills: ["Wireframing", "Figma", "User Flows", "UX Research"],
    lookingToLearn: ["Motion Design", "Web Development", "Publishing"],
    progress: 75,
    level: "Level 4",
  };

  const handleAction = (action) => console.log(`${action} clicked`);

  return (
    <div className="flex min-h-screen justify-center bg-gray-50 pt-10 pb-20">
      
      {/* Container Principal (simulează ecranul mobil) */}
      <div className="w-full max-w-sm bg-white shadow-2xl rounded-2xl overflow-hidden">
        
        {/* Header/Banner (Placeholder pentru Top Bar) */}
        <div className="bg-[#bdc7db] h-20 w-full relative">
          {/* Aici puteți adăuga iconițe de navigare dacă e nevoie, similar cu Edit Profile din imagine */}
        </div>

        {/* Conținutul Profilului */}
        <div className="relative p-6 -mt-10">
          
          {/* Avatar și Info de bază */}
          <div className="flex flex-col items-center">
            <Avatar className="h-24 w-24 border-4 border-white shadow-md">
              <AvatarImage src="/placeholder-avatar.jpg" alt={user.name} />
              <AvatarFallback className="bg-secondary text-white font-bold text-2xl">UT</AvatarFallback>
            </Avatar>
            <h1 className="text-xl font-bold text-primary mt-3">{user.name}</h1>
            <p className="text-sm text-gray-700">{user.role}</p>
            <div className="flex items-center space-x-1 text-sm text-muted-foreground mt-1">
                <MapPin className="h-3 w-3" />
                <span>{user.location}</span>
                <Star className="h-3 w-3 ml-2 text-yellow-500 fill-yellow-500" />
                <span>{user.rating}</span>
            </div>
          </div>
          
          {/* Descriere */}
          <p className="text-sm text-gray-600 mt-4 text-center">
            {user.description}
          </p>

          {/* Separator Vizual */}
          <div className="my-6">
            <div className="h-px bg-gray-200" />
          </div>

          {/* Secțiunea Skills */}
          <ProfileSection title="Skills:">
            {user.skills.map(skill => (
              <Badge key={skill} variant="secondary" className="bg-secondary/10 text-secondary hover:bg-secondary/20 rounded-full font-normal px-4 py-1">
                {skill}
              </Badge>
            ))}
          </ProfileSection>

          {/* Secțiunea Looking to learn */}
          <ProfileSection title="Looking to learn:">
            {user.lookingToLearn.map(skill => (
              <Badge key={skill} variant="outline" className="border-secondary text-secondary rounded-full font-normal px-4 py-1">
                {skill}
              </Badge>
            ))}
          </ProfileSection>
          
          {/* Card de Progres/Level (Cu ilustrație) */}
          <Card className="mt-8 p-4 rounded-xl border border-gray-100 shadow-sm bg-accent/20">
            <CardContent className="p-0 flex items-center justify-between">
              <div className="space-y-2 w-2/3">
                <p className="text-sm font-medium text-primary/80">Progress</p>
                <div className="relative h-2 rounded-full bg-gray-300">
                    <Progress value={user.progress} className="h-full bg-green-500 rounded-full [&>*]:bg-green-500" />
                </div>
                <div className="flex justify-between text-xs text-gray-600">
                    <span>{user.progress}%</span>
                    <span className="text-green-600 font-medium">{user.level}</span>
                </div>
              </div>
              
              {/* Ilustrație Copac/Progress */}
              <div className="w-1/4 flex justify-center">
                 {/* Placeholder pentru ilustrația cu copacul din Figma */}
                 <div className="w-12 h-12 bg-green-200 rounded-full flex items-center justify-center">
                    <Zap className="h-6 w-6 text-green-700" />
                 </div>
              </div>
            </CardContent>
          </Card>

          {/* Butoane de Navigare inferioare */}
          <div className="mt-8 space-y-3">
            <NavButton 
              icon={Settings} 
              text="Settings" 
              onClick={() => handleAction("Settings")}
            />
            <NavButton 
              icon={Edit2} 
              text="Edit profile" 
              onClick={() => handleAction("Edit profile")}
            />
             <NavButton 
              icon={User} 
              text="My Profile" 
              onClick={() => handleAction("My Profile")}
            />
          </div>
          
        </div>
      </div>
    </div>
  );
}